.banner{
    height: 448px;
    background-size: cover;
    color: white;
}
.content{
    padding-top: 140px;
    height: 190px;
    padding-left: 15px;
}
.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.button{
    color:white;
    background-color: rgb(51,51,51,0.5);
    border: none;
    outline: none;
    border-radius: 5px;
    font-weight: 700;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-bottom: 0.5rem;
    padding-top: 0.5rem;
    margin-right:1rem;
    cursor: pointer;
}
.button:hover{
    color:black;
    background-color: #e6e6e6;
}
.description{
    width: 45rem;
    height: 80px;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    max-width: 360px;
}
.fade{
    height: 7.4rem;
    background-image: linear-gradient(180deg,transparent,rgb(37,37,37,.61),#111);

}