.row{
    color:#fff;
    margin-left: 20px;
}
.posters{
    display:flex;
    padding:20px;
    overflow-x: scroll;
    overflow-y: hidden;
}
.poster{
    cursor: pointer;
    max-height: 250px;
    margin-right: 10px;
}
.posters::-webkit-scrollbar{
    display:none;
}
.small-poster{
    cursor: pointer;
    max-height:150px;
    margin-right: 10px;
}
.poster:hover{
    transform: scale(1.1);
}
.small-poster:hover{
    transform: scale(1.1);
}
